.box {
    margin: auto;
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}
.box p {
    color: white;
    font-weight: bold;
    font-size: 30px;
    line-height: 0px;
}
.box-char {
    width: 65px;
    height: 65px;
    border: 1px solid #3a3a3c;
}
.G {
    animation: zoom 0.5s ease;
    background-color: #538D4E;
}

.Y {
    animation: zoom 0.5s ease;
    background-color: #b59f3b;
}

.N {
    animation: zoom 0.5s ease;
    background-color: #3a3a3c;
}

@keyframes zoom {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

